import React, { useContext } from "react";
import { useDispatch, useSelector } from "react-redux";
import { formatterCurrency } from "../../../utils/string";
import MainContext from "../MainContext";
import { calculateSellsTotal, calculateShippingPrice } from "../helpers";
import { openModal } from "../../../actions/modal";
import { useTranslation } from "react-i18next";

function Totals() {
	const address = useSelector((state) => state.user.location.address);
	const dispatch = useDispatch();
	const { sells, sellsGroups, paymentMethodWithBalance } = useContext(
		MainContext
	);
	const { t } = useTranslation();
	function openMapModal(e) {
		dispatch(openModal("map"));
		e.preventDefault();
	}

	const sellsTotal = calculateSellsTotal(sells);
	const shippingPrice = calculateShippingPrice(sells, sellsGroups);
	const totalPrice = sellsTotal + shippingPrice - paymentMethodWithBalance;

	return (
		<div className="table-wrapper">
			<table className="table table-summary">
				<tbody>
					<tr className="summary-subtotal">
						<td>{t("total")}:</td>
						<td
							style={{
								width: "min-content",
							}}
						>
							{formatterCurrency(sellsTotal)}
						</td>
					</tr>
					<tr className="summary-shipping">
						<td>{t("delivery")}:</td>
						<td
							style={{
								width: "min-content",
							}}
						>
							{formatterCurrency(shippingPrice)}
						</td>
					</tr>
					<tr className="summary-shipping-estimate">
						<td
							style={{
								padding: "15px 0",
							}}
						>
							{address ? address.name : "  "} :
						</td>
						<td
							style={{
								padding: "15px 0",
							}}
						>
							<button
								style={{
									padding: "0",
									marginTop: "5px",
									textDecoration: "underline",
								}}
								onClick={openMapModal}
							>
								{t("change-address")}
							</button>
						</td>
					</tr>

					<tr className="summary-total">
						<td>{t("total")}:</td>
						<td
							style={{
								width: "min-content",
							}}
						>
							{formatterCurrency(totalPrice)}
						</td>
					</tr>
				</tbody>
			</table>
		</div>
	);
}

export default Totals;
