import React, { useContext, useState } from "react";
import { useTranslation } from "react-i18next";
import Modal from "react-modal";
import { useDispatch, useSelector } from "react-redux";
import { closeModal } from "../../../actions/modal";
import MainContext from "../../../views/checkout/MainContext";

export default function AddCommentModal() {
	const { comment, setComment } = useContext(MainContext);
	const [value, setValue] = useState();
	const customStyles = {
		content: {
			maxWidth: "100%"
		},
		overlay: {
			backgroundColor: "rgba(77,77,77,0.6)",
			zIndex: "10000"
		}
	};

	function close() {
		dispatch(closeModal("comment-modal"));
	}
	const { t } = useTranslation();
	const dispatch = useDispatch();
	const { modal, showModal } = useSelector(state => state.modal);

	return (
		<Modal
			isOpen={showModal && "comment" === modal}
			onRequestClose={close}
			style={customStyles}
			className="comment-modal"
			id="comment-modal"
		>
			<h3 className="text-center font-weight-bold">{t("add_comment")}</h3>
			<textarea
				onChange={e => setValue(e.target.value.trim())}
				defaultValue={comment}
				name="comment text"
				id="comment"
				cols="50"
				rows="5"
			></textarea>
			<button
				className="btn btn-primary"
				onClick={() => {
					close();
					setComment(value);
				}}
			>
				{t("save")}
			</button>
		</Modal>
	);
}
