import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
	finishSell,
	getCartSupplier,
	getUserCart,
	shippingCalculate,
	updateToCartApi,
} from "../../api/cart";
import { closeModal, openModal } from "../../actions/modal";
import LoaderOverlay from "../../components/features/loaders/LoaderOverlay";
import {
	getAvailableSellsIds,
	getDefaultShippingCalculateData,
	getShippingCalculateData,
	replaceSellFromSells,
	selectAvailableSellsForPaymentMethod,
	setSellsToSellsGroups,
	setSellsToSellGroup,
	setShippingServiceToSellGroup,
	setShippingServiceToSellsGroups,
	logger,
	filterSells,
} from "./helpers";
import AddCommentModal from "../../components/features/modal/AddCommentModal";
import YandexMapModalTwo from "../../components/features/modal/YandexMapModalTwo";
import MainContext from "./MainContext";
import SellGroupsTwo from "./sells/SellGroupsTwo";
import LoadingWrapper from "./LoadingWrapper";
import { removeToCartApi } from "../../api/cart";
import { toast } from "react-toastify";
import {
	removeFromCartState,
	changeQty,
	emptyCart,
	setUserCart,
} from "../../actions/cart";
import EmptyCart from "../../components/features/cart/EmptyCart";
import { useTranslation } from "react-i18next";
import { useHistory } from "react-router-dom";
import market from "../../config/market";
import RiveLoader from "../../components/features/loaders/RiveLoader";
import AddressList from "../profile/addresses/AddressList";
import SidebarThree from "./sidebar/SideBarThree";

const toastOptions = { position: "top-left" };

export default function CheckoutFour() {
	const dispatch = useDispatch();
	const h = useHistory();
	const { t } = useTranslation();
	const user = useSelector((state) => state.user);
	const { token } = useSelector((state) => state.user.tokenData);
	const {
		shippingServices,
		cashdesk: { paymentMethods },
	} = useSelector((state) => state.marketData);

	const [chooseCoordinate, setChooseCoordinate] = useState(false);
	const { coordinate, address } = user.location;
	const [sells, setSellsPlain] = useState([]);
	const [sellsGroups, setSellsGroups] = useState([]);
	const [loading, setLoading] = useState(true);
	const [loadingOverlay, setLoadingOverlay] = useState(false);
	const [paymentMethod, setPaymentMethod] = useState();
	const [paymentMethodWithBalance, setPaymentMethodWithBalance] = useState(0);
	const [extraDataSell, setExtraDataSell] = useState();
	const [userCards, setUserCards] = useState(null);
	const [userCard, setUserCard] = useState(null);
	const [comment, setComment] = useState();
	const [shippingDate, setShippingDate] = useState();
	const setSells = (sells) =>
		setSellsPlain(selectAvailableSellsForPaymentMethod(sells));

	let mount = true;
	const shippingAddress = useSelector((state) =>
		state.user.user?.addresses?.filter((a) => a.type === "shipping")
	);

	async function getInit() {
		dispatch(closeModal());
		try {
			let sells = await getUserCart(token);
			sells = filterSells(sells);

			if (sells.length) {
				let { data, shippingService } = getDefaultShippingCalculateData(
					sells,
					shippingServices,
					coordinate
				);

				const sellsGroups = await shippingCalculate(data, token);
				if (mount) {
					setSells(sells);
					dispatch(setUserCart(sells));
					setSellsGroups(
						setShippingServiceToSellsGroups(
							shippingService,
							sellsGroups
						)
					);
					logger(sellsGroups);
				}
				if (token) {
					getCartSupplier(token).then((res) => {
						if (mount) {
							setExtraDataSell(res);
						}
					});
				}
			} else {
				dispatch(emptyCart());
			}
			if (mount) {
				setLoading(false);
				setLoadingOverlay(false);
			}
		} catch (error) {
			console.log(error.response);
			alert(t("shipping_error"));
			if (
				error.response?.data?.error ||
				error.response?.data?.items ===
					"Sell store geolocation is not present"
			) {
				toast.error(t("geolocation_not_specified"), toastOptions);
			} else {
				toast.error(
					error.response?.data?.error || error.response?.data?.items,
					toastOptions
				);
			}
			if (mount) {
				setLoading(false);
				setLoadingOverlay(false);
			}
		}
	}

	function onChangePaymentMethod(payment) {
		setPaymentMethod(payment);
		setSellsPlain([
			...selectAvailableSellsForPaymentMethod(
				sells,
				payment,
				paymentMethods,
				extraDataSell
			),
		]);
	}

	function onFinishSell(payment) {
		setLoadingOverlay(true);
		finishSell(
			getAvailableSellsIds(sells),
			paymentMethod.id,
			!paymentMethod.cash && {
				bankCard: { id: payment.id },
			},
			comment,
			{
				countryCode: address.countryCode,
				administrativeArea: address.administrativeArea,
				locality: address.locality,
				dependentLocality: address.dependentLocality,
				addressLine1: address.addressLine1,
				latitude: coordinate.latitude,
				longitude: coordinate.longitude,
				postalCode: address.postalCode,
			},
			token,
			window.location.origin + "/profile",
			shippingDate
		)
			.then(({ url }) => {
				if (url) window.open(url, "_blank").focus();
				setLoadingOverlay(false);
				toast.success(t("purchese_sucsessfuly"), {
					position: "top-left",
				});
				h.push("/profile");
			})
			.catch(async (err) => {
				if (err.response?.data?.form?.children?.sells?.errors.length) {
					const notAvailable = [];

					for (const error of err.response?.data?.form?.children
						?.sells?.errors) {
						const sell = error.split(" ");
						sell.forEach((s) =>
							Number(s) ? notAvailable.push(Number(s)) : null
						);
					}

					for (const sell of sells) {
						for (const record of sell.sellRecords) {
							if (notAvailable.find((s) => s === record.id)) {
								record.message = t("amount-balance");
								setLoadingOverlay(true);
								await removeFromCart(
									record.id,
									record.variation,
									() => {}
								);
								setLoadingOverlay(false);
							}
						}
					}
					setLoadingOverlay(false);
					return sells;
				}
				toast.error(t("amount-balance"), toastOptions);
				console.log(err.response);
				setLoadingOverlay(false);
			});
	}

	async function onChangeShippingService(
		sellIds,
		shippingService,
		indexOfSellGroup,
		date
	) {
		try {
			setLoadingOverlay(true);

			const data = getShippingCalculateData(
				sellIds,
				shippingService,
				date,
				user.location.coordinate
			);

			if (data.shippingTime) {
				setShippingDate(data.shippingTime);
			}
			const [sellGroup] = await shippingCalculate(data, token);

			let sellsGroupsCopy = sellsGroups.slice();
			sellsGroupsCopy[indexOfSellGroup] = setSellsToSellGroup(
				sells,
				setShippingServiceToSellGroup(shippingService, sellGroup)
			);

			setSellsGroups(sellsGroupsCopy);

			setLoadingOverlay(false);
		} catch (error) {
			console.error(error);
			alert("error");
			setLoadingOverlay(false);
		}
	}

	function onChange(count, id, variationId, setLoadingRecord) {
		setLoadingRecord(true);
		if (!count) {
			removeFromCart(id, variationId, setLoadingRecord);
		} else {
			dispatch(changeQty(variationId, count, token));
			updateToCartApi(parseInt(count), id, token)
				.then((sell) => {
					setSells(replaceSellFromSells(sell, sells));
					setLoadingRecord(false);
				})
				.catch((e) => {
					console.error(e);
					setLoadingRecord(false);
				});
		}
	}

	function removeFromCart(id, variationId, setLoadingRecord) {
		dispatch(removeFromCartState(variationId));
		return removeToCartApi(id, token)
			.then((sell) => {
				setSells(replaceSellFromSells(sell, sells));
				setLoadingRecord(false);
			})
			.catch((e) => {
				setLoadingRecord(false);
				toast.error(e.response?.data?.message, toastOptions);
			});
	}

	useEffect(() => {
		if (mount) {
			setSellsGroups((sellsGroups) =>
				setSellsToSellsGroups(sells, sellsGroups)
			);
		}
	}, [sells]);

	useEffect(() => {
		if (!token) {
			h.push("/login", {
				prevPathname: h.location.pathname,
			});
		}
		if (!coordinate) {
			if (window.webview) {
				window.sendMessageToApp("OPEN_LOCATION");
			} else {
				setChooseCoordinate(true);
			}
		} else {
			getInit();
			setChooseCoordinate(false);
		}
	}, [coordinate]);

	function openMapModal(e) {
		dispatch(openModal("map"));
	}
	return (
		<MainContext.Provider
			value={{
				user: user,
				token: token,
				shippingServices: shippingServices,
				sells: sells,
				paymentMethods: paymentMethods,
				paymentMethodWithBalance: paymentMethodWithBalance,
				setPaymentMethodWithBalance: setPaymentMethodWithBalance,
				setSells: setSells,
				sellsGroups: sellsGroups,
				setSellsGroups: setSellsGroups,
				paymentMethod: paymentMethod,
				onFinishSell: onFinishSell,
				setPaymentMethod: setPaymentMethod,
				onChangePaymentMethod: onChangePaymentMethod,
				setLoadingOverlay: setLoadingOverlay,
				removeFromCart: removeFromCart,
				onChange: onChange,
				getAvailableSellsIds: getAvailableSellsIds,
				extraDataSell: extraDataSell,
				onChangeShippingService: onChangeShippingService,
				userCard: userCard,
				userCards: userCards,
				comment: comment,
				setComment: setComment,
				setUserCards: setUserCards,
				setUserCard: setUserCard,
			}}
		>
			<div className="main checkout-four">
				<div className="page-content">
					{chooseCoordinate ? (
						<div className="container">
							{shippingAddress.length > 0 ? (
								<AddressList
									shippingAddress={shippingAddress}
								/>
							) : null}
							<div className="d-flex align-item-center flex-column mt-3">
								<button
									className="btn bg-primary text-white mx-auto"
									onClick={() => {
										if (window.webview) {
											window.sendMessageToApp(
												"OPEN_LOCATION"
											);
										}
										openMapModal();
									}}
								>
									{t("address-not-selected")}
								</button>
								<h3 className="text-center mt-3">
									{t("choose_address")}
								</h3>
							</div>
						</div>
					) : loading ? (
						market.config.loader ? (
							<RiveLoader />
						) : (
							<LoaderOverlay wrapper={true} />
						)
					) : (
						<div className="cart">
							<div className="container">
								{sells.length ? (
									<LoadingWrapper loading={loadingOverlay}>
										<div className="col-md-8 col-12">
											<div className="change-address">
												<div className="summary-shipping-estimate">
													<h4> </h4>
													<button
														style={{
															padding: "0",
															marginTop: "5px",
															textDecoration:
																"underline",
														}}
														onClick={() => {
															console.log(
																"qwsdfdsfs"
															);
															openMapModal();
														}}
													>
														<p>
															{address
																? address.name
																: "  "}
														</p>
														<img
															src={
																market.config
																	.icons
																	.changeAddress
															}
															alt="icon"
														/>
													</button>
												</div>
											</div>
											<SellGroupsTwo
												sellsGroups={sellsGroups}
											/>
										</div>
										<div className="col-md-4 col-12">
											<SidebarThree
												setPaymentMethod={
													setPaymentMethod
												}
												sells={sells}
												extraDataSell={extraDataSell}
												onFinishSell={onFinishSell}
											/>
										</div>
									</LoadingWrapper>
								) : (
									<EmptyCart />
								)}
							</div>
						</div>
					)}
				</div>
				<YandexMapModalTwo />
				<AddCommentModal />
			</div>
		</MainContext.Provider>
	);
}
