import React, { useContext, useState } from "react";
import Modal from "react-modal";
import { useDispatch, useSelector } from "react-redux";
import { closeModal } from "../../../actions/modal";
import YandexMap from "../YandexMap";
import { setUserLocation } from "../../../actions/user";
import MainContext from "../../../views/checkout/MainContext";
import { useTranslation } from "react-i18next";

const customStyles = {
	content: {
		maxWidth: "100%",
		padding: "20px"
	},
	overlay: {
		backgroundColor: "rgba(77,77,77,0.6)",
		zIndex: "10000"
	}
};

function YandexMapModalTwo() {
	const { setLoadingOverlay } = useContext(MainContext);
	const showModal = useSelector(state => state.modal.showModal);
	const modal = useSelector(state => state.modal.modal);
	const { coordinate, address: addressUser, history } = useSelector(
		state => state.user.location
	);
	const { t } = useTranslation();
	const dispatch = useDispatch();

	const [center, setCenter] = useState({
		center: coordinate
			? [coordinate.latitude, coordinate.longitude]
			: [41.3112092101035, 69.27965188818546],
		zoom: 16
	});

	const [address, setAddress] = useState(
		addressUser
			? {
					...addressUser,
					name: addressUser.name
			  }
			: null
	);

	function closeMapModal() {
		dispatch(closeModal("map-modal-two"));
	}

	function changeAddress(h) {
		setCenter({
			center: [h.latitude, h.longitude],
			zoom: 16
		});
	}

	function saveAddress() {
		const h = {
			name: address.name,
			latitude: center.center[0],
			longitude: center.center[1]
		};
		const nh =
			history?.length > 0 ? history.filter(q => q.name !== h.name) : [];
		if (nh.length > 6) {
			nh.pop();
		}
		nh.unshift(h);

		const location = {
			coordinate: {
				latitude: center.center[0],
				longitude: center.center[1]
			},
			address,
			history: nh
		};
		dispatch(setUserLocation({ ...location }));
		dispatch(closeModal());
		setLoadingOverlay(true);
	}

	return (
		<Modal
			isOpen={showModal && "map-two" === modal}
			onRequestClose={closeMapModal}
			style={customStyles}
			className="map-modal-two"
			id="map-modal-two"
		>
			<h3 className="text-center font-weight-bold">
				{t("change-address")}
			</h3>
			<ul className="address-history">
				{history?.length > 0 &&
					history.map((h, i) => (
						<li onClick={() => changeAddress(h)} key={i}>
							{h.name}
						</li>
					))}
			</ul>
			<div className="map_content">
				<div className="location-wrapper">
					<img
						src="assets/images/pin.png"
						alt="location"
						width="22"
						height="22"
					/>
				</div>
				<p className="location-address">
					{address ? address.name : ""}
				</p>

				<YandexMap
					zoom={false}
					center={center}
					setCenter={setCenter}
					onChange={address => {
						setAddress(address);
					}}
					bottom={15}
					right={15}
				/>
			</div>
			<button onClick={saveAddress} className="btn bg-primary w-100">
				{t("save")}
			</button>
		</Modal>
	);
}

export default YandexMapModalTwo;
