import React, { useContext, useEffect, useState } from "react";
import SellTwo from "./SellTwo";
import MainContext from "../MainContext";
import { formatterCurrency } from "../../../utils/string";
import { dividerPro } from "../helpers";
import Vector from "../../../assets/images/Vector.png";
import { SlideToggle } from "react-slide-toggle";
import { useTranslation } from "react-i18next";
import { getPrice } from "../../../utils/oxMethods";
import market from "../../../config/market";
import isSuccessDate from "./isSuccessDate";

function getDeliveryTimeReadable(durationMinutes) {
	let time = {
		minut: 0,
		hour: 0,
		day: 0,
	};
	let data = dividerPro(durationMinutes, 60);
	time.minute = data.remain;

	if (data.result) {
		let { result, remain } = dividerPro(data.result, 24);
		time.day = result;
		time.hour = remain;
	}
	return time;
}

function timeRender({ day, minute, hour }) {
	let str =
		(day ? (day === 1 ? `${day} ` : `${day}  `) : "") +
		(hour ? (hour === 1 ? `${hour} ` : `${hour}  `) : "") +
		(minute ? `${minute}  ` : "");

	return str;
}

function SellGroupTwo({
	openShippingServiceModal,
	indexOfSellGroup,
	sellGroup,
	shippingServices,
	locations,
}) {
	const { extraDataSell } = useContext(MainContext);
	const [date, setDate] = useState(null);
	const total = sellGroup.sells.reduce((prev, next) => {
		return (prev += getPrice(next.subtotal));
	}, 0);

	const { removeFromCart, setLoadingOverlay } = useContext(MainContext);
	const [shippingServiceName, setShippingServiceName] = useState(
		shippingServices.find((s) => {
			return s.totalPrice.max
				? getPrice(s.totalPrice.max) >= total &&
						getPrice(s.totalPrice.min) <= total
				: 1;
		})?.name
	);

	async function removeAll(sells) {
		setLoadingOverlay(true);
		for (const sell of sells) {
			for (const record of sell.sellRecords) {
				if (!record.deleted) {
					await removeFromCart(record.id, record.variation, () => {});
				}
			}
		}
		setLoadingOverlay(false);
	}

	const { t } = useTranslation();

	const [collapse, setCollapse] = useState(false);
	const [workingDay, setWorkingDay] = useState({});
	const weekDays = [
		{ name: "Sunday" },
		{ name: "Monday" },
		{ name: "Tuesday" },
		{ name: "Wednesday" },
		{ name: "Thursday" },
		{ name: "Friday" },
		{ name: "Saturday" },
	];

	useEffect(() => {
		if (extraDataSell) {
			const locationData = extraDataSell.locations.find(
				(location) =>
					location.id === sellGroup.sells[0].sellRecords[0].location
			);
			const days = [];
			if (locationData?.workingDays) {
				for (const day of weekDays) {
					days.push({
						name: day.name,
						startTime:
							locationData.workingDays["startTime" + day.name],
						endTime: locationData.workingDays["endTime" + day.name],
					});
				}
			}
			const today = new Date().getDay();
			setWorkingDay(days[today]);
		}
	}, [extraDataSell]);

	return (
		<div className="cart-block-main">
			<div className="filter-item" key="cat">
				<SlideToggle collapsed={false}>
					{({ onToggle, setCollapsibleElement }) => (
						<div className="widget widget-collapsible">
							<div className="cart_delivery_info w-100 d-flex align-items-center justify-content-between">
								<div className="left">
									<div
										className="left_top d-flex align-items-center"
										onClick={() => {
											openShippingServiceModal({
												modal: true,
												sellGroup,
												indexOfSellGroup,
												setShippingServiceName,
												setDate,
											});
										}}
									>
										<h4 className="m-0 mr-2 d-flex align-items-center">
											<img
												src={
													market.config.home !== 5
														? Vector
														: market.config.icons
																.car
												}
												alt="vector"
												width="18"
												height="18"
											/>
											<p className="m-0">
												{shippingServiceName}
											</p>
										</h4>
										<span className="m-0">
											{formatterCurrency(
												sellGroup.shippingPrice
											)}
										</span>
									</div>
									<div className="left_bottom">
										<div className="d-flex align-items-center">
											<img
												src="/assets/images/time.svg"
												width="15"
												height="15"
												alt="time"
											/>

											<p className="m-0 mr-2">
												{date
													? date.date
													: t("soon-time")}
											</p>
											<span className="m-0 time">
												~
												{date
													? date.time
													: timeRender(
															getDeliveryTimeReadable(
																sellGroup.duration
															)
													  )}
											</span>
										</div>
									</div>
								</div>
								<div
									className="right"
									onClick={(e) => {
										setCollapse(!collapse);
										onToggle(e);
										e.preventDefault();
									}}
								>
									<i
										className={`icon-angle-down ${
											collapse ? "open" : ""
										}`}
									></i>
								</div>
							</div>
							<div
								style={{ overflow: "hidden" }}
								ref={setCollapsibleElement}
							>
								{workingDay
									? Object.keys(workingDay).length > 0
										? isSuccessDate(workingDay)
										: null
									: null}
								<div
									className="widget-body"
									style={{
										border: isSuccessDate(workingDay)
											? "1px solid #ffd0d0"
											: "",
									}}
								>
									<div className="sell_group">
										{sellGroup.sells.map((sell, i) => (
											<SellTwo
												key={i}
												sell={sell}
												extraDataSell={extraDataSell}
											/>
										))}
									</div>
								</div>
							</div>
						</div>
					)}
				</SlideToggle>
			</div>
			{market.config.home === 9 ? (
				<button
					className="btn remove-all"
					onClick={() => removeAll(sellGroup.sells)}
				>
					{t("clear-all")}
				</button>
			) : null}
		</div>
	);
}

export default SellGroupTwo;
