import React from "react";

function isSuccessDate(time) {
	if (!time) return null;
	const startTime = time?.startTime?.date.split(" ")[1].slice(0, 5);
	const endTime = time?.endTime?.date.split(" ")[1].slice(0, 5);
	const currentData = new Date().getHours();
	const isSuccessTime =
		currentData >= startTime?.slice(0, 2) &&
		currentData < endTime?.slice(0, 2);
	var discountTime = 0;
	if (currentData < startTime?.slice(0, 2)) {
		discountTime = startTime?.slice(0, 2) - currentData;
	} else {
		discountTime = 24 - currentData + Number(startTime?.slice(0, 2));
	}

	return !isSuccessTime ? (
		<div className="warning_sell_time">
			<p className="warning_title">
				     {discountTime} 
			</p>
			<div className="d-block">
				<div className="d-flex align-items-center justify-content-between">
					<div>
						<p className="mb-0">  </p>
						<p className="mb-0">
							<img
								src={require("../../../assets/images/Time.png")}
								width="15"
								height="15"
								alt="time"
							/>
							 -  c {startTime ? startTime : "10:00"} {" "}
							{endTime ? endTime : "22:00"}
						</p>
					</div>
					<img
						src="/assets/images/exp24/warning.png"
						alt="warning icon"
						width={22}
						height={22}
					/>
				</div>
			</div>
		</div>
	) : null;
}

export default isSuccessDate;
