import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
	setUserAddresses,
	setUserData,
	setUserLocation
} from "../../../actions/user";
import { getAddressByCoordinate } from "../../../api/mapAPI";
import {
	addNewShippingAddress,
	removeShippingAddress
} from "../../../api/user";

import AcseptModal from "../../../components/features/modal/AcseptModal";

export default function AddressList({ shippingAddress }) {
	const dispatch = useDispatch();
	const { location } = useSelector(state => state.user);
	const { token } = useSelector(state => state.user.tokenData);
	const user = useSelector(state => state.user.user);
	const tokenData = useSelector(state => state.user.tokenData);
	const [acseptModal, setAcseptModal] = useState({ item: null, open: false });

	function isClicked(address) {
		removeUserAddress(address.id, token);
	}
	async function changeAddress(address) {
		if (address.latitude && address.longitude) {
			const n = {
				coordinate: {
					latitude: address.latitude,
					longitude: address.longitude
				},
				address: {
					...address,
					name: address.addressLine1.split(",").slice(2)
				},
				history: location.history
					? [...location.history]
					: location.history
			};
			dispatch(setUserLocation({ ...n }));
		} else {
			const data = await getAddressByCoordinate(
				address.latitude,
				address.longitude
			);
			const res = await addNewShippingAddress(data, token);
			dispatch(setUserAddresses(res));
			dispatch(setUserLocation({ ...data }));
		}
	}

	function removeUserAddress(id) {
		dispatch(
			setUserData(
				{
					...user,
					addresses: user.addresses.filter(
						address => address.id !== id
					)
				},
				tokenData
			)
		);
		removeShippingAddress(id, token);
	}

	return shippingAddress.map((address, i) => (
		<div
			key={i}
			onClick={() => {
				changeAddress(address);
			}}
		>
			<div className="shipping-address">
				<p className="mb-0">{address.addressLine1}</p>
				<i
					onClick={() =>
						setAcseptModal({
							item: address,
							open: true
						})
					}
					className="icon-close"
				></i>
			</div>
			{acseptModal.open ? (
				<AcseptModal
					title="are_you_sure"
					isClicked={isClicked}
					acseptModal={acseptModal}
					setAcseptModal={setAcseptModal}
				/>
			) : null}
		</div>
	));
}
