import React, { useContext } from "react";
import MainContext from "../../MainContext";
import PaymentMethod from "./PaymentMethod";
// import InputBalancePayment from "./InputBalancePayment";

export default function PaymentMethods() {
	const { paymentMethods } = useContext(MainContext);

	return (
		<div className="payment_methods d-flex justify-content-between mt-3 mb-3 row">
			{/* <InputBalancePayment /> */}
			{paymentMethods?.length > 0 &&
				paymentMethods.map(payment =>
					payment.wallet ? null : (
						<PaymentMethod key={payment.id} payment={payment} />
					)
				)}
		</div>
	);
}
