import React, { useContext } from "react";
import { formatterCurrency } from "../../../utils/string";
import MainContext from "../MainContext";
import {
	calculateSellsDiscount,
	calculateSellsTotal,
	calculateShippingPrice
} from "../helpers";
import { useTranslation } from "react-i18next";

function Totals() {
	const { t } = useTranslation();
	const { sells, sellsGroups } = useContext(MainContext);
	const sellsTotal = calculateSellsTotal(sells);
	const sellsDiscount = calculateSellsDiscount(sells);
	const shippingPrice = calculateShippingPrice(sells, sellsGroups);

	return (
		<div className="table-wrapper">
			<div className="table table-summary">
				<div className="summary-subtotal">
					<p>{t("productstwo")}</p>
					<span className="dashed d-none" />
					<p className="priceSell">{formatterCurrency(sellsTotal)}</p>
				</div>
				{sellsDiscount > 0 ? (
					<div className="summary-subtotal discount">
						<p>{t("discount")}</p>
						<span className="dashed d-none" />
						<p className="priceSell">
							{formatterCurrency(sellsDiscount)}
						</p>
					</div>
				) : null}

				<div className="summary-shipping">
					<p>{t("delivery")}:</p>
					<span className="dashed d-none" />
					<p className="priceSell">
						{formatterCurrency(shippingPrice)}
					</p>
				</div>
			</div>
		</div>
	);
}

export default Totals;
