import React, { useContext } from "react";
import { useDispatch, useSelector } from "react-redux";
import MainContext from "../../MainContext";
import { closeModal } from "../../../../actions/modal";

export default function PaymentMethod({ payment }) {
	const showModal = useSelector(state => state.modal.showModal);
	const modal = useSelector(state => state.modal.modal);
	const dispatch = useDispatch();
	const { paymentMethod, onChangePaymentMethod } = useContext(MainContext);

	const isSelectedPayment = () => payment.id === paymentMethod?.id;

	function closePaymentModal() {
		if (showModal && "payment" === modal) {
			document
				.getElementById("payment-modal")
				.classList.remove("ReactModal__Content--after-open");

			setTimeout(() => {
				dispatch(closeModal("payment-modal"));
			}, 200);
		}
	}

	return (
		<div
			key={payment.id}
			className={`col-3 d-flex flex-column justify-content-between payment_wrapper rounded border p-0 ${
				isSelectedPayment() ? "selected" : ""
			}`}
			onClick={() => {
				onChangePaymentMethod(payment);
				closePaymentModal();
			}}
		>
			<img
				style={{
					width: "100%",
					height: "100%",
					display: "block",
					objectFit: "contain"
				}}
				src={`https://app.ox-sys.com${payment.icon}`}
				alt={payment.name}
				width="100"
			/>
			<p
				className="m-0 text-center"
				style={{
					fontSize: "14px",
					userSelect: "none"
				}}
			>
				{payment.name.split(" ")[0]}
			</p>
		</div>
	);
}
