import React from "react";
import Totals from "./Totals";
import { Link } from "react-router-dom";
import PaymentMethods from "./payment/PaymentMethods";
import { useTranslation } from "react-i18next";

function Sidebar({ onFinishSell, paymentMethod, openPaymentModal }) {
	const { t } = useTranslation();
	return (
		<div className="mt-2 sidebar">
			<div className="summary summary-cart">
				<Totals />
				<PaymentMethods />
			</div>

			<button
				className="btn btn-outline-primary-2 btn-order btn-block"
				onClick={!paymentMethod ? openPaymentModal : onFinishSell}
				// disabled={!(paymentMethod && evenSellsAvailable(sells))}
			>
				{t("pay")}
			</button>
			<Link
				to={`${process.env.PUBLIC_URL}/`}
				className="btn btn-outline-dark-2 btn-block mb-3 continue-shipping"
			>
				<span>{t("continue-shopping")}</span>
				<i className="icon-refresh"></i>
			</Link>
		</div>
	);
}

export default Sidebar;
