import React, { useContext } from "react";
import { useDispatch, useSelector } from "react-redux";
import Modal from "react-modal";
import { openModal } from "../../../actions/modal";
import market from "../../../config/market";
import MainContext from "../../../views/checkout/MainContext";
import {
	calculateSellsDiscount,
	calculateSellsTotal,
	calculateShippingPrice
} from "../../../views/checkout/helpers";
import { formatterCurrency } from "../../../utils/string";

export default function InfoSell({ isCheckOut, setIsCheckOut }) {
	const {
		userCard,
		sells,
		paymentMethod,
		onFinishSell,
		paymentMethods,
		sellsGroups,
		paymentMethodWithBalance
	} = useContext(MainContext);
	const dispatch = useDispatch();

	const { location } = useSelector(state => state.user);
	const yuuKassa = paymentMethods.find(p => p.gatewayType === "yuu_kassa");
	const cash = paymentMethods.find(p => p.cash === true);
	const sellsTotal = calculateSellsTotal(sells);
	const sellsDiscount = calculateSellsDiscount(sells);
	const shippingPrice = calculateShippingPrice(sells, sellsGroups);
	const totalPrice =
		sellsTotal + shippingPrice - paymentMethodWithBalance - sellsDiscount;

	function buy() {
		if (paymentMethod) {
			onFinishSell(userCard ? userCard : cash ? cash : yuuKassa);
			setIsCheckOut(false);
		} else dispatch(openModal("usercard"));
	}

	return (
		<Modal
			isOpen={isCheckOut}
			onRequestClose={() => setIsCheckOut(false)}
			style={{
				overlay: {
					zIndex: "9999"
				}
			}}
			className="info_sell_modal"
			id="info_sell_modal"
		>
			<div className="info_sell_content">
				<div className="info_sell_header d-flex align-items-center">
					<div onClick={() => setIsCheckOut(false)}>
						<i className="icon-arrow-left"></i>
					</div>
					<h3 className="w-100 text-center"> </h3>
				</div>
				<div className="info_sell_body">
					<div className="location d-flex align-items-center">
						<img
							src={market.config.icons.location}
							alt="location icon"
						/>
						<p>{location.address.addressLine1}</p>
						<img
							onClick={() => dispatch(openModal("map"))}
							src={market.config.icons.settings}
							alt="settings icon"
						/>
					</div>
					<div className="comment d-flex align-items-center">
						<img
							src={market.config.icons.comment}
							alt="comment icon"
						/>
						<p></p>
						<div
							onClick={() => dispatch(openModal("comment"))}
							className="d-flex align-items-center"
						>
							<p className="add m-0 mr-2"></p>
							<i className="icon-angle-right"></i>
						</div>
					</div>

					<div className="info_sell_payment">
						<h5></h5>
						<div className="payment">
							<img
								src={market.config.icons.wallet}
								alt="wallet icon"
							/>
							<p> </p>
							<div
								className="d-flex align-items-center"
								onClick={() => dispatch(openModal("usercard"))}
							>
								<p className="card-number">
									{userCard ? userCard.name : ""}
								</p>
								<i className="icon-angle-right"></i>
							</div>
						</div>
						<div className="promo_code">
							<img
								src={market.config.icons.promocode}
								alt="promocode icon"
							/>
							<p></p>
							<div className="d-flex align-items-center">
								<p className="add"></p>
								<i className="icon-angle-right"></i>
							</div>
						</div>
					</div>
					<div className="info_sell_price flex-grow-1">
						<h5> </h5>
						<div>
							<p></p>
							<p>{formatterCurrency(sellsTotal)}</p>
						</div>
						<div>
							<p></p>
							<p>{formatterCurrency(shippingPrice)}</p>
						</div>
						{sellsDiscount > 0 ? (
							<div>
								<p></p>
								<p>{formatterCurrency(sellsDiscount)}</p>
							</div>
						) : null}
						<div className="total">
							<p></p>
							<p>{formatterCurrency(totalPrice)}</p>
						</div>
					</div>
					<div className="button">
						<button
							className="btn btn-primary"
							onClick={() => {
								buy();
							}}
						>
							
						</button>
					</div>
				</div>
			</div>
		</Modal>
	);
}
