import React from "react";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import Modal from "react-modal";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { closeModal } from "../../../../actions/modal";
import {
	addUserCard,
	deleteUserCard,
	getUserCard,
	verifyCard,
} from "../../../../api/user";
import AddNew from "./AddNew";
import Verify from "./Verify";
import market from "../../../../config/market";

export default function AddUserCard({
	setUserCards,
	userCardTab,
	yuuKassa,
	multiCard,
	cash,
	userCards,
	userCard,
	setUserCard,
	onChangePaymentMethod,
	setPaymentMethod,
}) {
	const customStyles = {
		content: {
			maxWidth: "80%",
			transform: "translateY(-50%)",
			// padding: `${userCardTab ? `0px` : `20px`}`
		},
		overlay: {
			zIndex: 10000,
			backgroundColor: "rgba(77,77,77,0.6)",
		},
	};

	const dispatch = useDispatch();
	const [addNewCard, setAddNewCard] = useState(false);
	const [userCardId, setUserCardId] = useState(0);
	const [code, setCode] = useState(false);
	const [newCard, setNewCard] = useState(false);
	const { modal, showModal } = useSelector((state) => state.modal);
	const { token } = useSelector((state) => state.user.tokenData);
	const { t } = useTranslation();

	function close() {
		setCode(false);
		setAddNewCard(false);
		dispatch(closeModal("usercard"));
	}

	const submit = (values) => {
		addUserCard(
			values.card_number,
			values.card_validity_period_year +
				values.card_validity_period_month,
			token
		)
			.then((res) => {
				setNewCard(res);
				setCode(true);
			})
			.catch((e) => {
				console.log(e);
				close();
				toast.error(t("uncurrent_card"), {
					position: "top-left",
				});
			});
	};

	const verifysubmit = (values) => {
		verifyCard(values.code_number, newCard.id, token)
			.then(() => {
				userCardId && deleteUserCard(userCardId);
				toast.success(t("card_added"), { position: "top-left" });
				close();
				getUserCard(token).then((res) => {
					setUserCards(res);
				});
			})
			.catch(() => {
				toast.error(t("uncurrent_pass"), {
					position: "top-left",
				});
			});
	};

	return (
		<Modal
			isOpen={showModal && "usercard" === modal}
			style={customStyles}
			onRequestClose={close}
			className="usercard-modal"
			id="usercard-modal"
		>
			<div className={`cash-div`}>
				<h3
					className={`text-center font-weight-bold ${
						userCardTab ? `d-none` : ``
					}`}
				>
					{t("select-payment")}
				</h3>

				<form name="cards-form" className="form-group">
					<span
						onClick={() => {
							setUserCardId(0);
							setAddNewCard(true);
						}}
						style={{
							textDecoration: "underline",
							cursor: "pointer",
						}}
						className={`add-new-card ${
							market.config.home === 6 ? "" : "ml-auto"
						}`}
					>
						<i className="icon-plus mr-2"></i>
						<span>{t("add-card")}</span>
					</span>
					{userCards?.map((card) => {
						return (
							<div key={card.id} className="usercart-content">
								<label
									htmlFor={card.id}
									onClick={() => {
										setUserCard(card);
										setPaymentMethod(multiCard);
										onChangePaymentMethod(multiCard);
									}}
								>
									<input
										name="payment"
										id={card.id}
										type="radio"
										defaultChecked={
											userCard
												? userCard.id === card.id
												: false
										}
									/>
									<span className="radio">
										<span></span>
									</span>
									<span>{card.name}</span>
								</label>
								<span
									onClick={() => {
										setUserCardId(card.id);
										setAddNewCard(true);
									}}
									style={{
										textDecoration: "underline",
										cursor: "pointer",
									}}
									className={
										market.config.home === 6
											? ""
											: "ml-auto"
									}
								>
									{t("edit")}
								</span>
							</div>
						);
					})}

					{yuuKassa ? (
						<div>
							<label
								htmlFor="yoo"
								onClick={() => {
									setUserCard(yuuKassa);
									setPaymentMethod(yuuKassa);
									onChangePaymentMethod(yuuKassa);
								}}
							>
								<input
									name="payment"
									id="yoo"
									type="radio"
									defaultChecked={
										userCard
											? userCard.id === yuuKassa.id
											: false
									}
								/>
								<span className="radio">
									<span></span>
								</span>
								{yuuKassa?.name}
							</label>
						</div>
					) : null}

					{/* {market.config.home === 8 ? null : ( */}
					{cash ? (
						<div>
							<label
								htmlFor="cash"
								onClick={() => {
									setUserCard(cash);
									setPaymentMethod(cash);
									onChangePaymentMethod(cash);
								}}
							>
								<input
									name="payment"
									id="cash"
									type="radio"
									defaultChecked={
										userCard
											? userCard?.id === cash?.id
											: false
									}
								/>
								<span className="radio">
									<span></span>
								</span>
								{t("cash")}
							</label>
						</div>
					) : null}
					{/* )} */}
				</form>

				<button className="btn btn-primary" onClick={close}>
					{t("save")}
				</button>
				{(addNewCard || userCardTab) && <AddNew onSubmit={submit} />}
				{code && <Verify onSubmit={verifysubmit} />}
			</div>
		</Modal>
	);
}
