import React, { useEffect } from "react";
// import market from "../../../config/market";

function ShippingServices({
	list,
	value,
	onChange,
	setPickUp,
	addClass = "form-control"
}) {
	// var newList = [];
	// if (market.config.home === 9) {
	// 	newList = list.filter(item =>
	// 		value === 2 ? item.id !== 9 : value === 9 ? item.id !== 2 : list
	// 	);
	// 	console.log(newList);
	// }

	useEffect(() => {
		if (setPickUp) {
			setPickUp(list.find(s => s.id === parseInt(value))?.pickup);
		}
	}, [parseInt(value)]);

	return (
		list && (
			<form className="form-group col-lg-6 col-md-6 col-12">
				<select
					className={addClass}
					required={true}
					value={value}
					onChange={e => onChange(e.target.value)}
				>
					{// market.config.home === 9
					// 	? newList.map(service => (
					// 			<option value={service.id} key={service.id}>
					// 				{service.name}
					// 			</option>
					// 	  ))
					// 	:
					list.map(service => (
						<option value={service.id} key={service.id}>
							{service.name}
						</option>
					))}
				</select>
			</form>
		)
	);
}

export default ShippingServices;
