import React from "react";
import { useTranslation } from "react-i18next";
import Modal from "react-modal";
const customStyles = {
	content: {
		top: "40%",
		transform: "translateY(-60%)",
		padding: "20px",
	},
	overlay: {
		backgroundColor: "rgba(77,77,77,0.6)",
		zIndex: "10000",
	},
};

export default function SellFilterModal({ title, modal, setModal }) {
	const { t } = useTranslation();

	return (
		<Modal
			isOpen={modal.open}
			className="modal-dialog acsept-modal"
			onRequestClose={() => setModal({ open: false })}
			style={customStyles}
			id="acsept-modal"
		>
			<div className="modal-content">
				<h2 className="question text-center">{t(title)}</h2>
				<div className="acsept-buttons text-center">
					<button
						className="acsept-yes"
						onClick={() => {
							setModal({ open: false });
						}}
					>
						{t("okay_")}
					</button>
				</div>
			</div>
		</Modal>
	);
}
