import React from "react";
import SellRecordTwo from "./SellRecordTwo";
import { getSellLocation } from "../helpers";
import { useTranslation } from "react-i18next";
import market from "../../../config/market";
import SellRecordThree from "./SellRecordThree";
import isSuccessDate from "./isSuccessDate";
// import YandexMapLocationsModal from "../../../components/features/modal/YandexMapLocationsModal";

function SellTwo({ sell, extraDataSell }) {
	const { t } = useTranslation();
	// sell.sellRecords
	// 	.filter((s) => !s.deleted)
	// 	.map((sellRecord, index) => {
	// 		console.log(sellRecord.message);
	// 	});

	return (
		<>
			<div className={sell.available ? "active-sell" : "unactive-sell"}>
				<div className="info_sell">
					<p className="m-0 d-flex aling-items-center">
						<p
							style={{
								whiteSpace: "nowrap",
								marginBottom: "0",
							}}
						>
							{t("order")} {sell.id}
						</p>
						{isSuccessDate() ? (
							<div
								className="pending"
								style={{
									marginLeft: "5px",
									fontSize: "11px",
									lineHeight: "23px",
									backgroundColor: "rgb(252, 126, 126)",
									borderRadius: "5px",
									color: "rgb(255, 255, 255)",
									padding: "0px 7px",
								}}
							>
								
							</div>
						) : null}
					</p>
					{/* {console.log(extraDataSell)} */}
					{extraDataSell?.locations && (
						<div className="sell_location">
							<i className="icon-map-marker"></i>
							<span>
								{getSellLocation(
									extraDataSell.locations,
									sell.sellRecords[0].location
								)}
							</span>
						</div>
					)}
				</div>
				{sell.sellRecords
					.filter((s) => !s.deleted)
					.map((sellRecord, index) => {
						return market.config.home === 5 ||
							market.config.home === 8 ? (
							<SellRecordThree
								key={index}
								sellRecord={sellRecord}
								locations={extraDataSell?.locations}
								product={extraDataSell?.variations.find(
									(product) =>
										product.id === sellRecord.variation
								)}
							/>
						) : (
							<SellRecordTwo
								key={index}
								sellRecord={sellRecord}
								locations={extraDataSell?.locations}
								product={extraDataSell?.variations.find(
									(product) =>
										product.id === sellRecord.variation
								)}
							/>
						);
					})}
			</div>

			{/* {extraDataSell.locations ? (
				<YandexMapLocationsModal
					supplierAdress={extraDataSell?.locations}
				/>
			) : null} */}
		</>
	);
}

export default SellTwo;
