import React, { useContext, useEffect, useState } from "react";
import { getPrice, getImageUrl } from "../../../utils/oxMethods";
import { Link } from "react-router-dom";
import { quantityInputs } from "../../../utils";
import MainContext from "../MainContext";
import InputChangerCount from "../../../components/common/InputChangerCount";
import { getSellLocation } from "../helpers";
import Loader from "../../../components/features/loaders/loader";
import { useTranslation } from "react-i18next";

function SellRecord({ sellRecord, product, locations }) {
	const { removeFromCart, onChange } = useContext(MainContext);
	const [loadingRecord, setLoadingRecord] = useState(false);
	const { t } = useTranslation();

	useEffect(() => {
		quantityInputs();
	});

	return (
		<div className="cart-item cart-supplier-info p-0 pl-5 pr-5 flex-column align-items-start">
			{sellRecord.message ? (
				<div className="not-count-message">
					<p>{sellRecord.message}</p>
					
				</div>
			) : null}
			<div className="row cart-product-card w-100">
				<div className="col-lg-6 d-flex cart-product-info ml-0">
					<div className="cart-item-img pl-0 ml-0">
						<Link
							className="product-media"
							to={`${process.env.PUBLIC_URL}/product/${sellRecord.variation}`}
						>
							<img
								style={{
									width: "100%",
									height: "100%",
									objectFit: "cover"
								}}
								src={
									product
										? getImageUrl(product.images[0])
										: "#"
								}
								alt="Product"
							/>
						</Link>
					</div>
					<div className="cart-item-infoblock w-100">
						<Link
							className="text-dark"
							to={`${process.env.PUBLIC_URL}/product/${sellRecord.variation}`}
						>
							{sellRecord.variationName}
						</Link>
						<div className="d-flex">
							{product ? (
								product.properties.map((prop, index) => {
									return (
										<div
											key={index}
											className="cart-variation-pr"
										>
											<span>{prop.name}</span>
											<span>{prop.value}</span>
										</div>
									);
								})
							) : (
								<div className="short-line-shimmer"></div>
							)}
						</div>
						<div className="cart-variation-pr">
							{locations &&
								getSellLocation(locations, sellRecord.location)}
						</div>

						<div className="cart-price-mobileblock">
							<div className="cart-price-perone">
								{getPrice(sellRecord.finalPrice, true)}{" "}
								{t("for-unit")}
							</div>
							<div className="cart-amount-product">
								{getPrice(sellRecord.total, true)}
							</div>

							<div className="mob-product-action">
								{loadingRecord ? (
									<div className="loading-count">
										<Loader />
									</div>
								) : (
									<InputChangerCount
										initValue={sellRecord.count}
										onChange={value =>
											onChange(
												value,
												sellRecord.id,
												sellRecord.variation,
												setLoadingRecord
											)
										}
									/>
								)}
								<button
									className="btn-remove"
									onClick={() =>
										removeFromCart(
											sellRecord.id,
											sellRecord.variation
										)
									}
								>
									<i className="i-icon icon-times-circle-o"></i>
								</button>
							</div>
						</div>
					</div>
				</div>
				<div className="col-lg-3 priceblock-mobile">
					<div className="cart-price-perone">
						{getPrice(sellRecord.finalPrice, true)} {t("for-unit")}
					</div>
					<div className="cart-amount-product">
						{getPrice(sellRecord.total, true)}
					</div>
				</div>
				<div className="col-lg-2 cart-product-actions cart-product-actions-2">
					{loadingRecord ? (
						<div className="loading-count">
							<Loader />
						</div>
					) : (
						<InputChangerCount
							initValue={sellRecord.count}
							onChange={value =>
								onChange(
									value,
									sellRecord.id,
									sellRecord.variation,
									setLoadingRecord
								)
							}
						/>
					)}
					<button
						className="btn-remove"
						onClick={() =>
							removeFromCart(sellRecord.id, sellRecord.variation)
						}
					>
						<i className="i-icon icon-times-circle-o"></i>
					</button>
				</div>
			</div>
		</div>
	);
}

export default SellRecord;
