import React, { useEffect } from "react";
// import market from "../../../config/market";

function ShippingServicesTwo({ list, value, onChange, setPickUp }) {
	// var newList = [];
	// if (market.config.home === 9) {
	// 	newList = list.filter(item =>
	// 		value === 2 ? item.id !== 9 : value === 9 ? item.id !== 2 : list
	// 	);
	// 	console.log(newList);
	// }

	useEffect(() => {
		setPickUp(list.find(s => s.id === parseInt(value))?.pickup);
	}, [parseInt(value)]);
	return (
		list && (
			<div className="tab-nav pt-5 pb-5">
				{list.map((item, i) => (
					<div
						key={i}
						onClick={() => onChange(item.id)}
						className={`tab-item ${
							parseInt(value) === item.id ? `active` : ``
						}`}
					>
						{item.name}
					</div>
				))}
			</div>
		)
	);
}

export default ShippingServicesTwo;
