import React, { useContext, useEffect, useState } from "react";
import { getPrice, getImageUrl, removeProps } from "../../../utils/oxMethods";
import { Link } from "react-router-dom";
import { quantityInputs } from "../../../utils";
import MainContext from "../MainContext";
import InputChangerCount from "../../../components/common/InputChangerCount";
import { useSelector } from "react-redux";
import market from "../../../config/market";

function SellRecordThree({ sellRecord, product }) {
	const { onChange, removeFromCart } = useContext(MainContext);
	const [loadingRecord, setLoadingRecord] = useState(false);
	const { settings } = useSelector(state => state.marketData);
	const [properties, setProperties] = useState();

	useEffect(() => {
		if (product) {
			setProperties(
				removeProps(product.properties, settings.variationProperty)
			);
		}
	}, [product?.properties]);

	// async function removeProduct(id, item) {
	// 	setLoadingOverlay(true);
	// 	removeFromCartState(id);
	// 	if (token && item.id) {
	// 		await removeToCartApi(item.id, token);
	// 	}
	// 	setLoadingOverlay(false);
	// 	window.location.reload();
	// }
	useEffect(() => {
		quantityInputs();
	});
	return (
		<div className="cart-supplier-info cart-item p-0 pl-5 pr-5 flex-column align-items-start">
			{sellRecord.message ? (
				<div className="not-count-message">
					<p>{sellRecord.message}</p>
					
				</div>
			) : null}
			<div className="row cart-product-card w-100">
				<div
					className={`${
						market.config.home === 8 ? `col-2` : `col-3`
					}`}
				>
					<div className="product-media-wrapper">
						<Link
							className="product-media"
							to={`${process.env.PUBLIC_URL}/product/${sellRecord.variation}`}
						>
							<img
								style={{
									width: "100%",
									height: "100%",
									objectFit: "contain"
								}}
								src={getImageUrl(product?.images[0])}
								alt="Product"
							/>
						</Link>
					</div>
				</div>
				<div className="col-9 row m-0 d-flex cart-product-info m-0">
					<div className="cart-item-infoblock w-100">
						<Link
							className="text-dark"
							to={`${process.env.PUBLIC_URL}/product/${sellRecord.variation}`}
						>
							{sellRecord.variationName}
						</Link>
						<div className="d-md-block d-flex">
							{product ? (
								properties?.map((prop, index) => {
									return (
										<div
											key={index}
											className="cart-variation-pr"
										>
											<span>{prop.name}</span>
											<span>{prop.value}</span>
										</div>
									);
								})
							) : (
								<div className="short-line-shimmer"></div>
							)}
						</div>
					</div>
					<div className="row m-0 w-100 align-items-center">
						<div className="col-6 p-0 cart-amount-product">
							{sellRecord.discount[sellRecord.discount.first] >
							0 ? (
								<div className="search-result-price">
									<span className="new-price">
										{getPrice(sellRecord.finalPrice, true)}
									</span>
									<span className="old-price text-through">
										{getPrice(sellRecord.subtotal, true)}
									</span>
								</div>
							) : (
								getPrice(sellRecord.total, true)
							)}
						</div>

						<div className="col-6 cart-product-action">
							<InputChangerCount
								plus={
									<img
										src="/assets/images/exp24/Plus.svg"
										alt="plus"
									/>
								}
								minus={
									<img
										src="/assets/images/exp24/Minus.svg"
										alt="minus"
									/>
								}
								initValue={sellRecord.count}
								onChange={value =>
									onChange(
										value,
										sellRecord.id,
										sellRecord.variation,
										setLoadingRecord
									)
								}
								loadingRecord={loadingRecord}
							/>
						</div>
					</div>
				</div>
				{market.config.home === 8 ? (
					<div className="col-1">
						<button
							onClick={() =>
								removeFromCart(
									sellRecord.id,
									sellRecord.variation,
									setLoadingRecord
								)
							}
						>
							<img
								src={market.config.icons.deleteIcon}
								alt="delete"
							/>
						</button>
					</div>
				) : null}
			</div>
		</div>
	);
}

export default SellRecordThree;
