import React, { useContext } from "react";
import Sell from "./Sell";
import MainContext from "../MainContext";
import { formatterCurrency } from "../../../utils/string";
import ShippingServices from "./ShippingServices";
import { dividerPro } from "../helpers";
import { useTranslation } from "react-i18next";

function getDeliveryTimeReadable(durationMinutes) {
	let time = {
		minut: 0,
		hour: 0,
		day: 0
	};
	let data = dividerPro(durationMinutes, 60);
	time.minute = data.remain;

	if (data.result) {
		let { result, remain } = dividerPro(data.result, 24);
		time.day = result;
		time.hour = remain;
	}
	return time;
}

function timeRender({ day, minute, hour }) {
	const { t } = useTranslation();
	let str =
		(day
			? day === 1
				? `${day} ${t("day")}`
				: `${day} ${t("day2")} `
			: "") +
		(hour
			? hour === 1
				? `${hour} ${t("hour")}`
				: `${hour} ${t("hour2")}  `
			: "") +
		(minute ? `${minute} ${t("minute2")} ` : "");

	return str;
}

function SellGroup({ sellGroup, indexOfSellGroup }) {
	const {
		shippingServices,
		onChangeShippingService,
		extraDataSell
	} = useContext(MainContext);
	const currenService = shippingServices.find(
		s => s.id === sellGroup.shippingService
	);
	const date = null;

	return (
		<div className="cart-block-main mt-2">
			<div className="filter-item" key="cat">
				<div className="cart-supplier-info">
					<div className="p-0 col-lg-6 col-md-3 col-12 d-flex">
						<div className="custom-control custom-checkbox m-0 d-flex align-items-center">
							<div className="delivery-block-cart">
								<p className="m-0">
									{formatterCurrency(sellGroup.shippingPrice)}
								</p>
							</div>
						</div>
					</div>
					<div className="col-lg-6 col-md-9 col-12 delivery-block-cart d-flex align-items-center justify-content-end">
						<div className="row">
							<div className="col-lg-6 col-md-6 col-sm-12">
								{!currenService.pickup &&
									timeRender(
										getDeliveryTimeReadable(
											sellGroup.duration
										)
									)}
							</div>
							<ShippingServices
								list={shippingServices}
								value={sellGroup.shippingService}
								onChange={value => {
									onChangeShippingService(
										sellGroup.sellIds,
										parseInt(value),
										indexOfSellGroup,
										date
									);
								}}
							/>
						</div>
					</div>
				</div>
				<hr className="m-0" />
				{sellGroup.sells.map((sell, i) => (
					<Sell key={i} sell={sell} extraDataSell={extraDataSell} />
				))}
			</div>
		</div>
	);
}

export default SellGroup;
