import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useDispatch, useSelector } from "react-redux";
import { closeModal } from "../../../actions/modal";
import Calendar from "react-calendar";
import TimePicker from "react-time-picker";
import "react-calendar/dist/Calendar.css";
import "react-time-picker/dist/TimePicker.css";
import "react-clock/dist/Clock.css";
import { useTranslation } from "react-i18next";
import YandexMapLocations from "./YandexMapLocations";
import ShippingServicesTwo from "../../../views/checkout/sells/shippingServiceTwo";
import { getShippingCalculateDataLocation } from "../../../views/checkout/helpers";
import { shippingCalculate } from "../../../api/cart";

export default function ShippingServiceModal({
	modal,
	setModal,
	sells,
	onChangeShippingService,
	shippingServices,
}) {
	const dispatch = useDispatch();
	const user = useSelector((state) => state.user);
	const { coordinate } = user.location;
	const { token } = useSelector((state) => state.user.tokenData);
	const { t } = useTranslation();
	const [date, onChangeDate] = useState(new Date());
	const [time, onChangeTime] = useState(new Date().toLocaleTimeString("ru"));
	const [shippingServiceValue, setShippingServiceValue] = useState();
	const [showDate, setshowDate] = useState(false);
	const [showCalendar, setShowCalendar] = useState(false);
	const [showTime, setShowTime] = useState(false);
	const [pickUp, setPickUp] = useState();
	const [selectedLocation, setSelectedLocation] = useState();
	const [activeLocation, setActiveLocation] = useState();

	useEffect(() => {
		if (!shippingServiceValue && modal.modal) {
			setShippingServiceValue(modal.sellGroup.shippingService);
		}
		if (pickUp) {
			setSelectedLocation(
				shippingServices.find((s) => s.pickup === true).locations[0]
			);

			setActiveLocation(
				shippingServices.find((s) => s.pickup === true).locations[0].id
			);
		}
	}, [modal, pickUp]);
	function saveShippingService(shippingServiceValue, date) {
		modal.setShippingServiceName(
			shippingServices.find(
				(s) => s.id === parseInt(shippingServiceValue)
			).name
		);
		if (date) {
			modal.setDate(date);
		}

		onChangeShippingService(
			modal.sellGroup.sellIds,
			parseInt(shippingServiceValue),
			modal.indexOfSellGroup,
			date
				? {
						date:
							date.date.split(".")[2] +
							"-" +
							date.date.split(".")[0] +
							"-" +
							date.date.split(".")[1],
						time: date.time.split(":")[2]
							? date.time
							: date.time + ":00",
				  }
				: null
		);
		setModal({ modal: false });
	}
	function submit() {
		if (pickUp) {
			dispatch(closeModal("shippingServiceModal"));
			setModal({ modal: false });
		} else {
			saveShippingService(
				parseInt(shippingServiceValue),
				showDate
					? {
							date: date.toLocaleDateString("ru"),
							time: time,
					  }
					: null
			);
			dispatch(closeModal("shippingServiceModal"));
		}
	}
	async function onChangeLocation() {
		let data = await getShippingCalculateDataLocation(
			sells,
			parseInt(shippingServiceValue),
			activeLocation,
			coordinate
		);

		await shippingCalculate(data, token);
	}
	const customStyles = {
		content: {
			padding: "20px",
		},
		overlay: {
			backgroundColor: "rgba(77,77,77,0.6)",
			zIndex: "10000",
		},
	};
	return (
		<>
			{showCalendar ? (
				<div className="calendar-wrapper">
					<Calendar value={date} onChange={onChangeDate} />
					<button
						className="btn btn-primary change-date"
						onClick={() => setShowCalendar(false)}
					>
						OK
					</button>
				</div>
			) : null}
			{showTime ? (
				<div className="time-wrapper">
					<TimePicker value={time} onChange={onChangeTime} />
					<button
						className="btn btn-primary change-time"
						onClick={() => setShowTime(false)}
					>
						OK
					</button>
				</div>
			) : null}
			<Modal
				isOpen={modal.modal}
				onRequestClose={() => {
					setShowCalendar(false);
					setShowTime(false);
					setModal({ modal: false });
				}}
				style={customStyles}
				className={`shipping_service_modal ${pickUp ? `pickup` : ``}`}
				id="shipping_service_modal"
			>
				<div className="shipping_service_modal_content">
					<h3 className="text-center font-weight-bold">
						  
					</h3>
					<div className="shipping-service">
						<ShippingServicesTwo
							addClass={"form-control"}
							list={shippingServices}
							setPickUp={setPickUp}
							value={shippingServiceValue}
							onChange={(value) => {
								setShippingServiceValue(value);
							}}
						/>
					</div>
					{pickUp ? (
						<div className="row locations-map">
							<div className="col-md-3 col-12">
								<div className="locations">
									{shippingServices
										.find((s) => s.pickup === true)
										?.locations?.map((location, index) => (
											<button
												className={`location ${
													location.id ===
													activeLocation
														? `active`
														: ``
												}`}
												onClick={() => {
													setSelectedLocation({
														id: location.id,
														name: location.name,
														address:
															location.address,
													});
													setActiveLocation(
														location.id
													);
													onChangeLocation();
												}}
												key={index}
											>
												{location.name}
											</button>
										))}
								</div>
							</div>
							<div className="col-md-9 col-12">
								<div className="map">
									<YandexMapLocations
										supplierAdress={
											selectedLocation?.address
										}
										pickUp={pickUp}
									/>
								</div>
							</div>
						</div>
					) : null}
					<div className="times_heading d-flex align-items-center justify-content-between">
						<div
							className={`flex-grow-1 p-3 ${
								!showDate ? " active" : ""
							}`}
							onClick={() => {
								setshowDate(false);
								setShowCalendar(false);
								setShowTime(false);
							}}
						>
							{t("soon-time")}
						</div>
						<div
							className={`flex-grow-1 p-3 ${
								showDate ? " active" : ""
							}`}
							onClick={() => setshowDate(true)}
						>
							 
						</div>
					</div>
					{showDate && (
						<div className="times_body d-flex align-items-center justify-content-between">
							<div onClick={() => setShowCalendar(!showCalendar)}>
								{date.toLocaleDateString("ru")}
							</div>
							<div onClick={() => setShowTime(!showTime)}>
								{time}
							</div>
						</div>
					)}

					<button className="btn" onClick={submit}>
						{t("save")}
					</button>
				</div>
			</Modal>
		</>
	);
}
