import React from "react";
import SellRecord from "./SellRecord";

function Sell({ sell, extraDataSell }) {
	return (
		<div className={sell.available ? "active-sell" : "unactive-sell"}>
			{sell.sellRecords
				.filter((s) => !s.deleted)
				.map((sellRecord, index) => {
					return (
						<SellRecord
							key={index}
							sellRecord={sellRecord}
							locations={extraDataSell?.locations}
							product={extraDataSell?.variations.find(
								(product) => product.id === sellRecord.variation
							)}
						/>
					);
				})}
		</div>
	);
}

export default Sell;
