import React from "react";
import { Fragment } from "react";
import { useTranslation } from "react-i18next";
import { Field, reduxForm } from "redux-form";
import i18n from "../../../../i18n";

const validate = values => {
	const errors = {};
	if (!values.code_number || values.code_number.length !== 4) {
		errors.card_number = i18n.t("add_card_error");
	}
	return errors;
};

const renderField = props => {
	let {
		input,
		type,
		placeholder,
		className,
		meta: { touched, error }
	} = props;

	if (touched) {
		if (error) {
			className = className + " warning";
		} else {
			className = className + " correct";
		}
	}
	return (
		<Fragment>
			<input
				{...input}
				placeholder={placeholder}
				className={className}
				type={type}
			/>
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

function Verify({ handleSubmit }) {
	const { t } = useTranslation();
	return (
		<form
			className="verify_modal"
			name="verify-form"
			onSubmit={handleSubmit}
			noValidate
		>
			<div className="add_new_card_code">
				<h3>{t("enter_code3")}</h3>
				<Field
					className="code_input"
					type="number"
					name="code_number"
					value="code_number"
					component={renderField}
					placeholder={t("code")}
					required
				/>
				<button className="btn btn-primary" type="submit">
					{t("bind")}
				</button>
			</div>
		</form>
	);
}
export default reduxForm({
	form: "verifyCard",
	validate
})(Verify);
