import React, { Fragment, useContext, useState } from "react";
import { useDispatch } from "react-redux";
import { openModal } from "../../../actions/modal";
import ShippingServiceModal from "../../../components/features/modal/ShippingServiceModal";
import market from "../../../config/market";
import MainContext from "../MainContext";
import SellGroupThree from "./SellGroupThree";
import SellGroupTwo from "./SellGroupTwo";

export default function SellGroupsTwo({
	sellsGroups,
	location,
	sells,
	loadingOverlay,
	setLoadingOverlay
}) {
	const dispatch = useDispatch();
	const { shippingServices, onChangeShippingService } = useContext(
		MainContext
	);
	const [modal, setModal] = useState({
		modal: false
	});

	function openShippingServiceModal({
		modal,
		sellGroup,
		indexOfSellGroup,
		setShippingServiceName,
		setDate
	}) {
		setModal({
			modal: modal,
			sellGroup: sellGroup,
			indexOfSellGroup: indexOfSellGroup,
			setDate: setDate,
			setShippingServiceName: setShippingServiceName
		});
	}

	return (
		<div>
			{market.config.home === 5 ? (
				<div
					className={`change-location-banner ${
						!location ? `active` : ``
					}`}
				>
					<h3>     .</h3>
					<button
						className="map-opener"
						onClick={() => dispatch(openModal("map"))}
					>
						 
					</button>
				</div>
			) : null}
			{sellsGroups.map((sellGroup, i) => {
				return (
					<Fragment key={i}>
						{market.config.home === 8 ? (
							<SellGroupThree
								openShippingServiceModal={
									openShippingServiceModal
								}
								sellGroup={sellGroup}
								location={location}
								indexOfSellGroup={i}
								loadingOverlay={loadingOverlay}
								setLoadingOverlay={setLoadingOverlay}
								shippingServices={shippingServices}
							/>
						) : (
							<SellGroupTwo
								openShippingServiceModal={
									openShippingServiceModal
								}
								sellGroup={sellGroup}
								location={location}
								indexOfSellGroup={i}
								shippingServices={shippingServices}
							/>
						)}
						<ShippingServiceModal
							onChangeShippingService={onChangeShippingService}
							modal={modal}
							sells={sells}
							setModal={setModal}
							shippingServices={shippingServices}
						/>
					</Fragment>
				);
			})}
		</div>
	);
}
