import React, { useContext, useEffect, useState } from "react";
import { getPrice, getImageUrl, removeProps } from "../../../utils/oxMethods";
import { Link } from "react-router-dom";
import { quantityInputs } from "../../../utils";
import MainContext from "../MainContext";
import InputChangerCount from "../../../components/common/InputChangerCount";
import Loader from "../../../components/features/loaders/loader";
import market from "../../../config/market";
import { useSelector } from "react-redux";
import SellFilterModal from "../../../components/features/modal/SellFilterModal";

function SellRecordTwo({ sellRecord, product }) {
	const { removeFromCart, onChange } = useContext(MainContext);
	const [loadingRecord, setLoadingRecord] = useState(false);
	const { settings } = useSelector((state) => state.marketData);
	const [properties, setProperties] = useState();
	const [modal, setModal] = useState({ open: true, item: [] });
	useEffect(() => {
		if (product) {
			setProperties(
				removeProps(product.properties, settings.variationProperty)
			);
		}
	}, [product?.properties]);

	useEffect(() => {
		quantityInputs();
	});
	return (
		<div className="cart-supplier-info cart-item osto-cart p-0 pl-5 pr-5 flex-column align-items-start">
			{sellRecord.message ? (
				<div className="not-count-message">
					<SellFilterModal
						title={sellRecord.message}
						modal={modal}
						// isClicked={isClicked}
						setModal={setModal}
					/>
				</div>
			) : null}
			<div className="row cart-product-card w-100">
				<div className="col-3">
					<div className="product-media-wrapper">
						<Link
							className="product-media"
							to={`${process.env.PUBLIC_URL}/product/${sellRecord.variation}`}
						>
							<img
								style={{
									width: "100%",
									height: "100%",
									objectFit: "contain",
								}}
								src={getImageUrl(product?.images[0])}
								alt="Product"
							/>
						</Link>
					</div>
				</div>
				<div className="col-7 d-flex cart-product-info m-0">
					<div className="cart-item-infoblock w-100">
						<Link
							className="text-dark"
							to={`${process.env.PUBLIC_URL}/product/${sellRecord.variation}`}
						>
							{sellRecord.variationName}
						</Link>
						<div className="d-md-block d-flex">
							{product ? (
								properties?.map((prop, index) => {
									return (
										<div
											key={index}
											className="cart-variation-pr"
										>
											<span>{prop.name}</span>
											<span>{prop.value}</span>
										</div>
									);
								})
							) : (
								<div className="short-line-shimmer"></div>
							)}
						</div>
						<div className="cart-amount-product">
							{sellRecord.discount[sellRecord.discount.first] >
							0 ? (
								<div className="search-result-price">
									<span className="new-price">
										{getPrice(sellRecord.finalPrice, true)}
									</span>
									<span className="old-price text-through">
										{getPrice(sellRecord.subtotal, true)}
									</span>
								</div>
							) : (
								getPrice(sellRecord.total, true)
							)}
						</div>
					</div>
				</div>
				{loadingRecord ? (
					<div
						className={`loading-count ${
							market.config.home === 6 ? `col-2` : ``
						}`}
					>
						<Loader />
					</div>
				) : (
					<div className="col-2 cart-product-actions">
						<InputChangerCount
							initValue={sellRecord.count}
							onChange={(value) =>
								onChange(
									value,
									sellRecord.id,
									sellRecord.variation,
									setLoadingRecord
								)
							}
						/>
						<button
							className="btn-remove"
							onClick={() =>
								removeFromCart(
									sellRecord.id,
									sellRecord.variation,
									setLoadingRecord
								)
							}
						>
							<i className="i-icon icon-times-circle-o"></i>
						</button>
					</div>
				)}
			</div>
		</div>
	);
}

export default SellRecordTwo;
