import React, { useEffect } from "react";
import Checkout from "./Checkout";
import CheckoutTwo from "./CheckoutTwo";
import CheckoutThree from "./CheckoutThree";
import market from "../../config/market";
import { Redirect } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setCategoryPath } from "../../actions/any";
import { useTranslation } from "react-i18next";
import CheckoutFour from "./CheckoutFour";

export default function ShopRoute() {
	const token = useSelector((state) => state.user.tokenData);
	const dispatch = useDispatch();
	const { t } = useTranslation();
	useEffect(() => {
		dispatch(setCategoryPath(null));
		document.head.querySelector("title").innerText = t("check_out");
	}, [dispatch]);

	if (!token) {
		return <Redirect to="/cart" />;
	} else {
		switch (market.config.checkout) {
			case 1:
				return <Checkout />;
			case 2:
				return <CheckoutTwo />;
			case 3:
				return <CheckoutThree />;
			case 4:
				return <CheckoutFour />;
			default:
				return <Checkout />;
		}
	}
}
