import React, { Fragment, useState } from "react";
import { Field, reduxForm } from "redux-form";
import { useTranslation } from "react-i18next";
import Loader from "../../loaders/loader";

const validate = values => {
	const errors = {};

	if (!values.card_number || values.card_number.length !== 19) {
		errors.card_number = "     16 ";
	}
	if (values?.card_number?.length >= 19) {
		values.card_number = values.card_number.substring(0, 19);
	}
	if (
		values.card_validity_period_month &&
		(values.card_validity_period_month.length > 2 ||
			values.card_validity_period_month < 0 ||
			values.card_validity_period_month > 12)
	) {
		values.card_validity_period_month = 12;
	}
	if (values && !parseInt(values.card_validity_period_month)) {
		errors.card_validity_period_month = "    2 ";
	}
	if (values.card_validity_period_month < 0) {
		errors.card_validity_period_month = "   ";
	}
	if (
		(values.card_validity_period_year &&
			values.card_validity_period_year.length > 2) ||
		values.card_validity_period_year < 0 ||
		values.card_validity_period_y > 22
	) {
		values.card_validity_period_year = 22;
	}
	if (values && !parseInt(values.card_validity_period_year)) {
		errors.card_validity_period_year = "    2 ";
	}
	return errors;
};

const renderField = props => {
	let {
		input,
		placeholder,
		className,
		id,
		disabled,
		type,
		meta: { touched, error }
	} = props;

	if (touched) {
		if (error) {
			className = className + " warning";
		} else {
			className = className + " correct";
		}
	}
	return (
		<Fragment>
			<input
				{...input}
				disabled={disabled}
				placeholder={placeholder}
				className={className}
				id={id}
				type={type}
			/>
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

const cardInput = props => {
	let {
		input,
		placeholder,
		className,
		disabled,
		id,

		meta: { touched, error }
	} = props;

	if (touched) {
		if (error) {
			className = className + " warning";
		} else {
			className = className + " correct";
		}
	}
	return (
		<Fragment>
			<input
				{...input}
				placeholder={placeholder}
				className={className}
				id={id}
				disabled={disabled}
				onChange={e => {
					e.target.value =
						e.target.value
							.match(/\d/g)
							?.join("")
							.slice(0, 16)
							.match(/.{1,4}/g)
							.join(" ") || "";
					input.onChange(e);
				}}
				type={"text"}
			/>
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

function typing(e) {
	if (e.target.value.length === 19) {
		document.getElementById("card_validity_period_month").focus();
	}
	if (
		document.getElementById("card_number_input").value.length >= 19 &&
		document.getElementById("card_validity_period_month").value.length >= 2
	) {
		document.getElementById("card_validity_period_year").focus();
	}
	if (
		document.getElementById("card_number_input").value.length >= 19 &&
		document.getElementById("card_validity_period_year").value.length >= 2
	) {
		document.getElementById("add_card_submit-button").focus();
	}
}

function AddNew({ handleSubmit }) {
	const [loading, setLoading] = useState(false);

	const { t } = useTranslation();
	return (
		<form
			className="add_new_card text-center"
			onSubmit={handleSubmit}
			noValidate
		>
			<h3>{t("add-card")}</h3>
			<Field
				className="new_card_input"
				type="number"
				name="card_number"
				id="card_number_input"
				onChange={e => typing(e)}
				disabled={loading}
				value="card_number"
				component={cardInput}
				placeholder={t("card_number")}
				required
			/>
			<div className="new_card_inputs">
				<div className="d-flex flex-column">
					<Field
						className="new_card_input"
						type="number"
						name="card_validity_period_month"
						value="card_validity_period_month"
						component={renderField}
						disabled={loading}
						onChange={e => (e.target.value > 0 ? typing(e) : false)}
						placeholder={t("month")}
						id={"card_validity_period_month"}
						required
					/>
				</div>
				<span>/</span>
				<div className="d-flex flex-column">
					<Field
						className="new_card_input"
						type="number"
						id={"card_validity_period_year"}
						name="card_validity_period_year"
						onChange={e => typing(e)}
						value="card_validity_period_year"
						component={renderField}
						disabled={loading}
						placeholder={t("year")}
						required
					/>
				</div>
			</div>
			<button
				type="submit"
				onClick={() => setLoading(true)}
				className="btn btn-primary"
				id="add_card_submit-button"
			>
				{loading ? <Loader /> : t("add")}
			</button>
		</form>
	);
}

export default reduxForm({
	form: "addNewCardForm",
	validate
})(AddNew);
