import React from "react";
import { useTranslation } from "react-i18next";
import Modal from "react-modal";
const customStyles = {
	content: {
		top: "40%",
		transform: "translateY(-60%)",
		padding: "20px"
	},
	overlay: {
		backgroundColor: "rgba(77,77,77,0.6)",
		zIndex: "10000"
	}
};

export default function AcseptModal({
	title,
	acseptModal,
	setAcseptModal,
	isClicked
}) {
	const { t } = useTranslation();

	return (
		<Modal
			isOpen={acseptModal.open}
			className="modal-dialog acsept-modal"
			onRequestClose={() => setAcseptModal({ open: false })}
			style={customStyles}
			id="acsept-modal"
		>
			<div className="modal-content">
				<h2 className="question">{t(title)}</h2>
				<div className="acsept-buttons">
					<button
						className="acsept-no"
						onClick={() => setAcseptModal({ open: false })}
					>
						{t("no")}
					</button>
					<button
						className="acsept-yes"
						onClick={() => {
							isClicked(acseptModal.item);
							setAcseptModal({ open: false });
						}}
					>
						{t("okay_")}
					</button>
				</div>
			</div>
		</Modal>
	);
}
