import React, { useContext, useState } from "react";
import TotalsTwo from "./TotalsTwo";
import { useDispatch, useSelector } from "react-redux";
import {
	calculateSellsDiscount,
	calculateSellsTotal,
	calculateShippingPrice,
} from "../helpers";
import { getUserCard } from "../../../api/user";
import { formatterCurrency } from "../../../utils/string";
import { debounce } from "../../../utils/debounce";
import MainContext from "../MainContext";
import vector2 from "../../../assets/images/Vector2.png";
import commentIcon from "../../../assets/images/CommentIcon.png";
import { openModal } from "../../../actions/modal";
import { useEffect } from "react";
import AddUserCard from "../../../components/features/modal/UserCard/AddUserCardModal";
import { useTranslation } from "react-i18next";
import market from "../../../config/market";
import PaymentMethods from "./payment/PaymentMethods";
import { removeFromCartState } from "../../../actions/cart";
import InfoSell from "../../../components/features/modal/InfoSell";
import { getFranchise } from "../../../api/cart";
import YandexMap from "../../../components/features/modal/YandexMapModal";

function SidebarTwo({
	location,
	sells,
	onFinishSell,
	setPaymentMethod,
	loader,
}) {
	const {
		sellsGroups,
		paymentMethodWithBalance,
		paymentMethods,
		paymentMethod,
		userCard,
		userCards,
		onChangePaymentMethod,
		setUserCard,
		setUserCards,
		comment,
		getAvailableSellsIds,
	} = useContext(MainContext);

	const dispatch = useDispatch();
	const address = useSelector((state) => state.user.location.address);
	const { token } = useSelector((state) => state.user.tokenData);
	const sellsTotal = calculateSellsTotal(sells);
	const sellsDiscount = calculateSellsDiscount(sells);
	const shippingPrice = calculateShippingPrice(sells, sellsGroups);
	const totalPrice =
		sellsTotal + shippingPrice - paymentMethodWithBalance - sellsDiscount;
	const [scrollBottom, setScrollBottom] = useState(false);
	const locations = useSelector((state) => state.user.user.roleLocationIds);
	const [switcher, setSwitcher] = useState(false);
	const [isCheckOut, setIsCheckOut] = useState(false);
	const [switcherTwo, setSwitcherTwo] = useState(false);
	const { t } = useTranslation();
	const multiCard = paymentMethods.find(
		(p) => p.gatewayType === "multicard_payment_collection"
	);
	const yuuKassa = paymentMethods.find((p) => p.gatewayType === "yuu_kassa");
	const allowDebt = useSelector((state) => state.marketData.allowDebt);
	const cash = paymentMethods.find((p) => p.cash === true);
	const sellIds = getAvailableSellsIds(sells);
	const userType = useSelector((state) => state.user.user.type);

	// const [franchise, setFranchise] = useState();

	const [dataInfo, setDataInfo] = useState(
		locations.length > 0
			? {
					options: {
						location: locations[0].id,
						margin: 1,
						roundingTarget: null,
						willReview: false,
					},
			  }
			: null
	);

	const rounding = [
		`${t("no_round")}`,
		`${t("round_up_to_1")}`,
		`${t("round_up_to_0.5")}`,
		`${t("round_up_to_100")}`,
		`${t("round_up_to_500")}`,
		`${t("round_up_to_800")}`,
		`${t("round_up_to_900")}`,
		`${t("round_up_to_1000")}`,
	];

	function openMapModal(e) {
		dispatch(openModal("map"));
		e.preventDefault();
	}

	let scrollEvent = () => {
		debounce(
			() => {
				const scrollToBottom =
					window.innerHeight +
					window.scrollY -
					document.body.scrollHeight;

				if (-scrollToBottom < 80) {
					setScrollBottom(true);
				} else {
					setScrollBottom(false);
				}
			},
			"scrollPay",
			10
		);
	};

	function openPayment() {
		dispatch(openModal("usercard"));
	}

	useEffect(() => {
		window.addEventListener("scroll", scrollEvent);
		getUserCard(token).then((res) => {
			setUserCards(res);
		});
		return () => {
			window.removeEventListener("scroll", scrollEvent);
		};
	}, []);

	const cart = useSelector((state) => state.cartlist.cart);

	function clicked() {
		if (paymentMethod) {
			if (dataInfo && sellIds && userType === "employee") {
				sellIds.forEach((id) => {
					getFranchise(id, dataInfo, token);
				});
			}
			onFinishSell(userCard ? userCard : cash ? cash : yuuKassa);
			for (let i = 0; i < cart.length; i++) {
				sells.map((item) => {
					if (
						item.sellRecords.find(
							(record) => record.stock === cart[i].data.stock.id
						)
					) {
						dispatch(
							removeFromCartState(cart[i].data.variation.id, true)
						);
					}
					return () => {};
				});
			}
		} else openPayment();
	}
	function roundingCheck(e) {
		const str = e.target.value;
		const regex = /\d/;
		const doesItHaveNumber = regex.test(str);
		if (doesItHaveNumber) {
			const NUMERIC_REGEXP = /[-]{0,1}[\d]*[.]{0,1}[\d]+/g;
			const numbers = e.target.value.match(NUMERIC_REGEXP);
			setDataInfo({
				...dataInfo,
				options: {
					...dataInfo.options,
					roundingTarget: numbers[0],
				},
			});
		}
	}

	return (
		<div className="sidebar_two">
			{market.config.home !== 5 ? (
				<div className="summary summary-cart">
					<div>
						<TotalsTwo />
						<div className="summary-shipping-estimate">
							<p>{address ? address.name : "  "}</p>
							<p>
								<button
									style={{
										padding: "0",
										marginTop: "5px",
										textDecoration: "underline",
									}}
									onClick={openMapModal}
								>
									<img
										src={vector2}
										alt="vector"
										width="15"
										height="15"
									/>
								</button>
							</p>
						</div>

						<div className="summary-user_card">
							<PaymentMethods />
							<div className="payments d-flex align-items-center justify-content-between w-100">
								<p>{t("payment")}:</p>
								<p
									style={{
										cursor: "pointer",
									}}
									onClick={() => openPayment()}
								>
									{userCard
										? userCard.name
										: t("select-payment")}
								</p>
							</div>
						</div>
						<div className="promo_code d-none">
							<p>{t("promocode")}</p>
							<p className="add">{t("add")}</p>
						</div>
					</div>

					<div
						className="summary-add_comment"
						onClick={() => dispatch(openModal("comment"))}
					>
						<img
							src={commentIcon}
							alt="comment icon"
							width="20"
							height="20"
						/>
						<p>{comment ? comment : t("add_comment")}</p>
					</div>

					<div
						className="summary-pay-info"
						style={{ opacity: scrollBottom ? "1" : "0" }}
					>
						<div className="d-flex align-items-center justify-content-between">
							<p>{t("total_count")}</p>

							<p>{formatterCurrency(totalPrice)}</p>
						</div>
						{allowDebt && locations.length > 0 ? (
							<div className="debt-trading">
								<div className="debt-body">
									<div className="debt-location">
										<span>
											{t("auto_arrival_location")}
										</span>
										<select
											name=""
											id=""
											className="form-control"
											onChange={(e) => {
												setDataInfo({
													...dataInfo,
													options: {
														...dataInfo.options,
														location: locations.find(
															(item) =>
																item.name ===
																e.target.value
														).id,
													},
												});
											}}
										>
											{locations.map((item, i) => (
												<option name="location" key={i}>
													{item.name}
												</option>
											))}
										</select>
									</div>
									<div className="will-rewiev">
										<span>
											{t("arrival_with_inventory")}
										</span>
										<div
											className={`switcher ${
												switcher ? `active` : ``
											}`}
											onClick={() => {
												setSwitcher(!switcher);
												setDataInfo({
													...dataInfo,
													options: {
														...dataInfo.options,
														willReview: !dataInfo
															.options.willReview,
													},
												});
											}}
										>
											<button className="switch" />
										</div>
									</div>
									<div className="will-rewiev">
										<span>{t("change_sale_price")}</span>
										<div
											className={`switcher ${
												switcherTwo ? `active` : ``
											}`}
											onClick={() => {
												setSwitcherTwo(!switcherTwo);
											}}
										>
											<button className="switch" />
										</div>
									</div>
									{switcherTwo ? (
										<div className="percent-round-up">
											<span>{t("product_markup")}</span>
											<div className="debt-info">
												<input
													type="text"
													className="form-control"
													onChange={(e) => {
														setDataInfo({
															...dataInfo,
															options: {
																...dataInfo.options,
																margin: parseFloat(
																	e.target
																		.value
																),
															},
														});
													}}
													defaultValue="1.00"
												/>
												<select
													name=""
													id=""
													className="form-control"
													onChange={(e) =>
														roundingCheck(e)
													}
												>
													{rounding.map((item, i) => (
														<option
															name="rounding"
															key={i}
														>
															{item}
														</option>
													))}
												</select>
											</div>
										</div>
									) : null}
									<div className="warning">
										<div className="img">
											<img
												src="/assets/images/megaphone.svg"
												alt="warning"
											/>
										</div>
										<div className="text-content">
											<p>
												<b>{t("attention")}</b>
											</p>
											<p>{t("after_confirming")}</p>
										</div>
									</div>
								</div>
							</div>
						) : null}

						<button
							disabled={
								getAvailableSellsIds(sells).length === 0 ||
								!paymentMethod ||
								loader
							}
							className="btn btn-primary"
							onClick={clicked}
						>
							{paymentMethod
								? paymentMethod.cash
									? t("pay")
									: t("pay_two")
								: t("select-payment")}
						</button>
					</div>

					<div
						className={`pay_wrapper`}
						// style={{ ...styleNav, transition: "0.3s" }}
					>
						<div className="pay_info">
							<p>{t("payment_count")}</p>
							<p className="sale_price">
								{formatterCurrency(totalPrice)}
							</p>
						</div>

						<button
							disabled={
								getAvailableSellsIds(sells).length === 0 ||
								loader
							}
							className="btn btn-outline-primary-2 btn-order btn-block pay-btn"
							onClick={() =>
								paymentMethod
									? onFinishSell(
											userCard
												? userCard
												: cash
												? cash
												: yuuKassa
									  )
									: openPayment()
							}
						>
							{t("pay")}
						</button>
					</div>
				</div>
			) : (
				<div
					className={`pay_wrapper`}
					// style={{ ...styleNav, transition: "0.3s" }}
				>
					<div className="pay_info">
						<p className="sale_price">
							{formatterCurrency(totalPrice)}
						</p>
					</div>

					<button
						className={`btn btn-primary btn-order btn-block pay-btn`}
						disabled={
							(market.config.home === 5 && !location) || loader
						}
						onClick={() => setIsCheckOut(!isCheckOut)}
					>
						 
					</button>
				</div>
			)}

			<AddUserCard
				setUserCards={setUserCards}
				onChangePaymentMethod={onChangePaymentMethod}
				userCards={userCards}
				userCard={userCard}
				yuuKassa={
					market.subdomain === "demomarketplace" ? yuuKassa : null
				}
				multiCard={multiCard}
				cash={cash}
				setPaymentMethod={setPaymentMethod}
				setUserCard={setUserCard}
			/>
			<YandexMap />
			<InfoSell isCheckOut={isCheckOut} setIsCheckOut={setIsCheckOut} />
		</div>
	);
}

export default SidebarTwo;
